@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  html {
    scroll-behavior: smooth;
  }
  
  body {
    font-family: 'Inter', system-ui, sans-serif;
  }
}

@layer components {
  .btn-primary {
    @apply bg-accent text-black px-8 py-3 rounded-lg font-semibold hover:bg-blue-400 transition-colors duration-300;
  }
  
  .btn-secondary {
    @apply border border-accent text-accent px-8 py-3 rounded-lg font-semibold hover:bg-accent hover:text-black transition-colors duration-300;
  }
  
  .section-title {
    @apply text-4xl font-bold text-center mb-16;
  }
  
  .card {
    @apply bg-dark-gray rounded-lg overflow-hidden hover:transform hover:scale-105 transition-transform duration-300;
  }
  
  .skill-item {
    @apply flex items-center space-x-2;
  }
  
  .skill-dot {
    @apply w-2 h-2 bg-accent rounded-full;
  }
}
